



















import { Component, Vue } from "vue-property-decorator";
import ExpressWord from "@/components/ExpressWord.vue";
import ExpressDefinition from "@/components/ExpressDefinition.vue";
import api from "@/data/apis";
import { Word } from "@/types/word";
import { SquareGrid } from "vue-loading-spinner";

@Component({
  components: {
    ExpressWord,
    ExpressDefinition,
    SquareGrid,
  },
})
export default class WordsGroup extends Vue {
  currentWords: Word[] = [];
  waiting = false;
  count = 4;

  async mounted(): Promise<void> {
    this.fetchWords();
  }

  async fetchWords(): Promise<void> {
    if (!this.waiting) {
      this.waiting = true;
      this.currentWords = [];
      for (let i = 0; i < this.count; i++) {
        const data = await api.randomWord();
        this.currentWords.push(data.body);
      }
      this.waiting = false;
    }
  }
}
